import './sincronizar.html';
import '../../globals/loading.html';
import {Pesquisas} from '../../../api/pesquisas/pesquisas';
import {Token} from '../../../api/token/token';
import {HTTP} from 'meteor/http';

const API = Meteor.settings.public.api;

const URLS = {
    login: API.login,
    salvar_entrevista : API.salvar_entrevista,
    pesquisas: API.pesquisas
};

let template;

Template.sincronizar.onCreated(()=>{

    template = Template.instance();

    // tenta conectar ao servidor se nao esta conectado
    setInterval(function () {
        Meteor.reconnect();
    }, 15 * 1000); // 15 segundos

    // faz o controle do icone de carregando
    template.buscandoDados = new ReactiveVar(false);

    template.subscribe('Pesquisas');
    template.subscribe('Token');

    template.pesquisas = () => {
        return Pesquisas.find({});
    };

    template.entrevistasPorBairro = () => {

        let pesquisas = Pesquisas.find({}).fetch(),
            obj = {}, arr = [], bairros = [];

        // Calcula o número de entrevistas realizadas
        pesquisas.map((pesquisa) => {

            // Se existe alguma entrevista relacionada à pesquisa
            if(pesquisa.entrevistas) {

                // salva todos os bairros em um hash
                pesquisa.bairros.map(bairro => {
                    bairros[pesquisa._id + bairro.codigoBairro] = bairro;
                });

                pesquisa.entrevistas.map((e) => { // e => eh a entrevista

                    let cod = pesquisa._id + e.bairro;

                    obj[cod] = obj[cod] + 1 || 1;
                });
            }
        });

        // Monta o array que deve conter o nome do bairro e a quantidade de entrevistas para ele
        for(prop in obj) {
            arr.push({bairro: bairros[prop].bairro, qut: obj[prop]})
        }

        return arr;
    };

});

Template.sincronizar.helpers({

    'entrevistasPorBairro' : () => {

        return template.entrevistasPorBairro();

    },
    'estaConectado' : () => {

        return Meteor.status().status === 'connected';
    },
    'buscandoDados' : () => {

        return template.buscandoDados.get();
    }

});

Template.sincronizar.events({

    'click #sincronizar' (event) {

        event.preventDefault();

        template.buscandoDados.set(true);

        let tokenObj = Token.findOne();

        if(!tokenObj) {
            Bert.alert('Registre um token antes de sincronizar!', 'danger');
            template.buscandoDados.set(false);
            return;
        }

        let token = tokenObj.token;

        let options = {
            data: {
                token : token
            }
        };

        Meteor.call('sincronizarPesquisas', {url: URLS.pesquisas, options}, (error, data) => {

            if(error) {
                Bert.alert(error.reason, 'danger');
                template.buscandoDados.set(false);
                return;
            }

            let status = data.data.status,
                msg = data.data.msg;

            if(data.statusCode !== 200 || status === 404) {
                Bert.alert(msg, 'danger');
                template.buscandoDados.set(false);
                return;
            }

            let pesquisasLocais = Pesquisas.find({}).fetch(),
                pesquisasServidor = data.data || [];

            // Insere localmente as pesquias obtidas do servidor
            inserirPesquisasLocalmente(pesquisasServidor);

            // Numero de entrevistas que devem ser salvas no servidor
            let entrevistasASalvar = getNumEntrevistasASalvar(pesquisasLocais);

            // Se não é necessário sincronizar as entrevistas
            if(entrevistasASalvar === 0) {

                let msg = 'Nenhuma entrevista necessita de ser sincronizada!';

                if(pesquisasServidor.length > 0) {
                    msg += '\nEntretanto, foram obtidadas <b>' + pesquisasServidor.length + '</b> pesquisas!'
                }

                Bert.alert(msg, 'info', 'growl-top-right');

                template.buscandoDados.set(false);

                return;
            }

            // Se eh necessario sincronizar entrevistas
            sincronizarEntrevistas(template, pesquisasLocais, token);


        });

        return;
        // Obtem as pesquisas
        HTTP.post(URLS.pesquisas, options, (error, data) => {

            if(error) {
                Bert.alert(error, 'danger');
                template.buscandoDados.set(false);
                return;
            }

            let status = data.data.status,
                msg = data.data.msg;

            if(status === 404) {
                Bert.alert(msg, 'danger');
                template.buscandoDados.set(false);
                return;
            }

            let pesquisasLocais = Pesquisas.find({}).fetch(),
                pesquisasServidor = data.data || [];

            // Insere localmente as pesquias obtidas do servidor
            inserirPesquisasLocalmente(pesquisasServidor);

            // Numero de entrevistas que devem ser salvas no servidor
            let entrevistasASalvar = getNumEntrevistasASalvar(pesquisasLocais);

            // Se não é necessário sincronizar as entrevistas
            if(entrevistasASalvar === 0) {

                let msg = 'Nenhuma entrevista necessita de ser sincronizada!';

                if(pesquisasServidor.length > 0) {
                    msg += '\nEntretanto, foram obtidadas <b>' + pesquisasServidor.length + '</b> pesquisas!'
                }

                Bert.alert(msg, 'info', 'growl-top-right');

                template.buscandoDados.set(false);

                return;
            }

            // Se eh necessario sincronizar entrevistas
            sincronizarEntrevistas(template, pesquisasLocais, token);

        });

    },
    'click .sinc.disabled' () {
        Bert.alert('É necessário estar conectado à internet para sincronizar!', 'info', 'growl-top-right');
    }

});

/**
 * Envia as entrevistas locais para o servidor
 * @param template
 * @param pesquisasLocais
 * @param token
 */
const sincronizarEntrevistas = (template, pesquisasLocais, token) => {

    // clona o objeto
    let _pesquisasLocais = JSON.parse(JSON.stringify(pesquisasLocais));

    // Retira a propriedade entrevistasRealizadas
    delete _pesquisasLocais.entrevistasRealizadas;

    let options = {
        data: {
            pesquisas : JSON.stringify(_pesquisasLocais),
            token: token
        }
    };

    Meteor.call('sincronizarSalvarEntrevistas', {url: URLS.salvar_entrevista, options}, (error, data) => {

        if(error) {

            Bert.alert('Erro de sincronização dos dados', 'danger');

            // Restaura os dados
            inserirPesquisasLocalmente(pesquisasLocais);

        }
        else {

            let status = data.data.status,
                msg = data.data.msg,
                pesquisas = data.data.pesquisas;

            if(status === 404) {

                Bert.alert(msg, 'danger');

                // Restaura os dados
                inserirPesquisasLocalmente(pesquisasLocais);

                return;
            }

            inserirPesquisasLocalmente(pesquisas);

            Bert.alert('Dados sincronizados com sucesso!', 'success');
        }

        template.buscandoDados.set(false);

    });

};

/**
 * Insere as pesquisas no localStorage
 * @param pesquisas
 */
const inserirPesquisasLocalmente = pesquisas => {

    // Limpa a collection local
    Pesquisas.clear();

    // Insere as pesquisas obtidas do servidor
    pesquisas.map((pesquisa) => {
        Pesquisas.insert(pesquisa);
    });

};

/**
 * Obtém a quantidade total de entrevistas locais contidas nas pesquisas
 * @param pesquisas
 */
const getNumEntrevistasASalvar = pesquisas => {

    let entrevistasASalvar = 0;

    pesquisas.map((pesquisa) => {

        // verifica se existe entrevistas
        if(pesquisa.entrevistas) {
            entrevistasASalvar += pesquisa.entrevistas.length;
        }
    });

    return entrevistasASalvar;

};