
Template.__checkName("token");
Template["token"] = new Template("Template.token", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n        ", HTML.DIV({
    "class": "col-sm-12"
  }, "\n            Token:\n            ", HTML.INPUT({
    id: "tokenValue",
    type: "text",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("token"), "token"));
    },
    placeholder: "Insira um token"
  }), "\n            ", HTML.INPUT({
    id: "_id",
    type: "hidden",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("token"), "_id"));
    }
  }), "\n            ", HTML.SPAN({
    id: "feedback",
    "class": function() {
      return Spacebars.mustache(view.lookup("msgClass"));
    }
  }, Blaze.View("lookup:msg", function() {
    return Spacebars.mustache(view.lookup("msg"));
  })), "\n        "), "\n    ");
}));
