import './token.html';
import { Token } from  '../../../api/token/token';

let template;

Template.token.onCreated(() => {

    template = Template.instance();

    template.subscribe('Token');

    template.msg = new ReactiveVar("Sem alterações");
    template.msgClass = new ReactiveVar('');

});

Template.token.helpers({

    'token': () => {
        return Token.findOne({});
    },
    'msg': () => {
        return template.msg.get();
    },
    'msgClass': () => {
        return template.msgClass.get();
    }

})

Template.token.events({

    'keyup #tokenValue': () => {

        template.msg.set('Salvando...');
        template.msgClass.set('msg-default');

        let _id = $('#_id').val(),
            token = $('#tokenValue').val();

        // Se nao há token salvo
        if(_id === "") {
            Token.insert({token: token});
        }
        else {
            Token.update({_id: _id}, {$set: {token: token}}, err => {

                if(err) {
                    template.msg.set('Não foi possível salvar suas alterações');
                    template.msgClass.set('msg-danger');
                    return;
                }

                setTimeout(function () {
                    template.msg.set('Alterações salvas com sucesso!');
                    template.msgClass.set('msg-success');
                }, 300);

            });
        }
    }

});