import { Template } from 'meteor/templating';
import {Pesquisas} from '../../api/pesquisas/pesquisas';
import './header.html';

Template.header.onCreated(() => {

    let template = Template.instance();

    template.subscribe('Pesquisas');

    template.pesquisas = () => {
        return Pesquisas.find({});
    }

    template.numEntrevistas = () => {

        let pesquisas = Pesquisas.find({}).fetch();

        let totalEntrevistas = 0;

        // Calcula o número de entrevistas realizadas
        pesquisas.map((pesquisa) => {

            // se existe entrevista
            if(pesquisa.entrevistas){
                totalEntrevistas += pesquisa.entrevistas.length;
            }
        });

       return totalEntrevistas;
    };

});

Template.header.helpers({

    'existePesquisa' () {
        return Template.instance().pesquisas().count() > 0;
    },
    'numEntrevistasNaoSinc' () {
        return Template.instance().numEntrevistas();
    }

});

Template.header.events({
    
    'click #token': (evt) => {

        evt.preventDefault();

        let res = confirm('Tem certeza que deseja alterar o token?');

        if(res) {
            FlowRouter.go('token');
        }

    },
    'click nav a' (evt) {

        let rota = FlowRouter.current().route.name;

        if(rota === "add_entrevista" && $('form').length > 0) {

            if(evt.target.className.indexOf("novaEntrevista") >= 0) {
                return false;
            }

            let cancelar = confirm('Deseja cancelar a entrevista atual?');

            if(!cancelar) {
                evt.preventDefault();
                return false;
            }
        }
    }
    
});