import './add_entrevista.html';
import '../../globals/loading.html';

import { Pesquisas } from '../../../api/pesquisas/pesquisas';
import {moment} from 'meteor/momentjs:moment';

moment.locale('pt-br');

_ = lodash;

const NAO_RESPONDIDO = "-",
    AUSENTE = "Ausente",
    NAO = "Não";

let template;

Template.add_entrevista.onCreated(() => {

    template = Template.instance();

    let handle = template.subscribe('Pesquisas');

    template.pesquisa = new ReactiveVar();
    template.bairros = new ReactiveVar([]);
    template.cidadeSelecionada = new ReactiveVar();
    template.regiaoSelecionada = new ReactiveVar();
    template.bairroSelecionado = new ReactiveVar();
    template.regiaoCodigoSelecionada = new ReactiveVar();
    template.bairroCodigoSelecionado = new ReactiveVar();
    template.isCarregando = new ReactiveVar(true);
    template.numQuestoes = new ReactiveVar(0);
    template.idUltimaQuestao = new ReactiveVar("#");

    template.autorun(function () {

        if(handle) {
            template.pesquisas = () => {
                return Pesquisas.find();
            }

            template.cidades = () => {
                return Pesquisas.find().fetch().map((pesquisa) => {
                    return {nome: pesquisa.cidade, _id: pesquisa._id};
                });
            };

            // Coloca o icone de loading para uma melhor experiencia de usuario
            setTimeout(function () {
                template.isCarregando.set(false);
            }, 500);

        }

    });

});

Template.add_entrevista.helpers({

    'cidades': () => {
        return template.cidades();
    },
    'bairros': () => {
        return template.bairros.get();
    },
    'cidadeSelecionada': () => {
        return template.cidadeSelecionada.get();
    },
    'regiaoSelecionada': () => {
        return template.regiaoSelecionada.get();
    },
    'bairroSelecionado': () => {
        return template.bairroSelecionado.get();
    },
    'regiaoCodigoSelecionada': () => {
        return template.regiaoCodigoSelecionada.get();
    },
    'bairroCodigoSelecionado': () => {
        return template.bairroCodigoSelecionado.get();
    },
    'ehBairroSelecionado': (bairro) => {
        return bairro === template.bairroSelecionado.get();
    },
    'isCarregando': () => {

        if(template.isCarregando.get()) {
            $('body').css('pointer-events', 'none');
        }
        else {
            $('body').css('pointer-events', 'auto');
        }

        setTimeout(function () {
            // Eh necessario aplicar novamente as funcoes da mascara, pois elas se perdem quando o formulario
            // eh re-renderizado
            aplicarMascaras();
        }, 1000);

        return template.isCarregando.get();
    },
    'idUltimaQuestao': () => {
        return template.idUltimaQuestao.get();
    }

});

Template.add_entrevista.onRendered (() => {

    setTimeout(function() {

        // descobre o numero de questoes
        let numQuestoes = $('section').length - 1;

        template.numQuestoes.set(numQuestoes);
        template.idUltimaQuestao.set("q" + numQuestoes.pad(3));

        aplicarValidacoes();

        aplicarEventosCondicionais();

        aplicarMascaras();

        $('#q008-nascimento').datepicker({
            language: 'pt-BR',
            disableTouchKeyboard: true,
            autoclose: true,
            clearBtn: true,
            startDate: '01/01/1900',
            endDate: 'today',
            maxViewMode: 'years',
            orientation: 'left bottom'
        });

    }, 1000);

});

Template.add_entrevista.events({

    'click #next' (event) {

        event.preventDefault();

        let active = $('.active-question');

        // Obtem o id da questao atual
        let id = $(active).attr("id");

        // Se id for da ultima questao, nao faz nada
        if(id === "last") {
            return;
        }

        // Descobre proxima questao
        let active_next = "q" + (Number(id.substring(1)) + 1).pad(3);

        // Se questao atual eh a ultima, encaminha para questao last
        if(id === template.idUltimaQuestao.get()) {
            active_next = "last"
        }

        // somente se for a primeira questao eh que verifica se a entrevista ja foi realizada antes
        if(id === "q001") {

            let regiao = $('#q001-regiao').val(),
                bairro = $('#q001-bairro').val(),
                quadra = $('#q001-quadra').val(),
                lote = $('#q001-lote').val(),
                edificacao = $('#q001-edificacao').val();

            let pesquisa = template.pesquisa.get(),
                entrevistas = pesquisa.entrevistas;

            // Se a pesquisa possui entrevistas
            if(entrevistas) {

                // Checa se uma entrevista para a mesma regiao, bairro, quadra, lote e edificacao ja nao foi realizada
                for(let i = 0; i < entrevistas.length; i++) {

                    let entrevista = entrevistas[i],
                        respostas = entrevista.respostas,
                        subPerguntas = respostas[0].subPerguntas,
                        aceite = respostas[2].subPerguntas[0].resposta;

                    // FIXME Critico, depende da ordem das perguntas no HTML
                    let subPerguntaRegiao = subPerguntas[0].resposta,
                        subPerguntaBairro = subPerguntas[1].resposta,
                        subPerguntaQuadra = subPerguntas[2].resposta,
                        subPerguntaLote = subPerguntas[3].resposta,
                        subPerguntaEdificacao = subPerguntas[4].resposta;

                    if(regiao === subPerguntaRegiao && bairro === subPerguntaBairro
                        && quadra === subPerguntaQuadra && lote === subPerguntaLote && edificao === subPerguntaEdificacao) {

                        // somente se a entrevista ja foi realizada, mas o morador estava Ausente, eh que sera
                        // permitido continuar
                        if(aceite !== AUSENTE) {
                            Bert.alert('Entrevista já realizada para essa mesma Região, Bairro, Quadra, Lote e Edificação', 'danger');
                            return;
                        }
                    }

                }
            }

            // Também verifica se essa entrevista já foi realizada em outra sincronizacao (dados vindos do servidor)
            let entrevistasRealizadas = pesquisa.entrevistasRealizadas;

            for(let i = 0; i < entrevistasRealizadas.length; i++) {

                let entrevista = entrevistasRealizadas[i];

                if(entrevista.regiao === regiao && entrevista.bairro === bairro
                    && entrevista.quadra === quadra && entrevista.lote === lote
                    && entrevista.edificacao === edificacao &&  entrevista.aceite !== AUSENTE) {

                    Bert.alert('Entrevista já realizada para essa mesma Região, Bairro, Quadra, Lote e Edificação', 'danger');
                    return;
                }
            }

        }

        let validForm = $('form').valid();

        if(!validForm) {
            Bert.alert('O formulário contém erro(s)', 'danger', 'growl-bottom-right');
            return;
        }

        // da um scroll pro comeco da pagina novamente
        window.scrollTo(0, 0);

        $(active).removeClass('active-question').addClass('not-active-question');
        $('#' + active_next).removeClass('not-active-question').addClass('active-question');

        focarPrimeiroCampo();

    },
    'click #prev' (event) {

        event.preventDefault();

        // da um scroll pro comeco da pagina novamente
        window.scrollTo(0, 0);

        let active = $('.active-question');

        // Obtem o id da questao atual
        let id = $(active).attr("id");

        // Se for a primeira questao, nao faz nada
        if(id.indexOf("q001") !== -1) {
            return;
        }

        // Descobre questao anterior
        let active_prev = "q" + (Number(id.substring(1)) - 1).pad(3);

        // Se a questao atual eh da section last, retorna para a ultima questao
        if(id === "last") {
            active_prev = template.idUltimaQuestao.get();
        }

        $(active).removeClass('active-question').addClass('not-active-question');
        $('#' + active_prev).removeClass('not-active-question').addClass('active-question');

        focarPrimeiroCampo();

    },
    'click #finalizarQuestionario' (event) {

        event.preventDefault();

        template.isCarregando.set(true);

        salvarQuestionario(template);
    },
    'click [name="menu-cidade"]' (event) {

        event.preventDefault();

        // se elemento possui classe selecionada, des-seleciona ele
        if($(event.target).hasClass('selected')) {
            $('.tabela-bairros').addClass('invisible');
            $(event.target).removeClass('selected');
            return;
        }

        // seleciona a nova cidade
        $('.selected').removeClass('selected');
        $(event.target).addClass('selected');

        let cidade = event.target.innerHTML;

        template.pesquisa.set(Pesquisas.findOne({_id: event.target.id}));

        let bairros = Pesquisas.findOne({cidade:cidade}).bairros;

        // Pega somente o nome dos bairros
        bairros = bairros.map(bairro => {
            return bairro.bairro;
        });

        // substitui espacos
        cidade = cidade.replace(/ /g, '-');

        bairros = bairros.map((bairro) => {
            return {id:cidade + '--' + bairro.replace(/ /g, '-'),cidade: cidade, bairro: bairro};
        });

        // ordena os bairros alfabeticamente
        bairros.sort((a,b) => {
            if(a.bairro < b.bairro) {
                return -1;
            }
            else if(a.bairro > b.bairro) {
                return 1;
            }
            else {
                return 0;
            }
        });

        Template.instance().bairros.set(bairros);

        // coloca a tabela de bairros visível
        $('.tabela-bairros').removeClass('invisible');
    },
    'click .bairrosSelecionaveis' (event) {

        event.preventDefault();

        let bairro = event.target,
            info = bairro.id.split('--'),
            bairroNome = info[1].replace(/-/g, ' '),
            cidadeNome = info[0].replace(/-/g, ' ');

        // esconde a selecao de bairro e cidade
        $('[name="selecao-informacoes-iniciais"]').addClass('invisible');

        // habilita o preenchimento do formulario
        $('form').removeClass('invisible');

        focarPrimeiroCampo();

        bairro = getBairroByNome(bairroNome, Pesquisas.findOne({cidade: cidadeNome}));


        template.bairroSelecionado.set(bairro.bairro);
        template.bairroCodigoSelecionado.set(bairro.codigoBairro);
        template.cidadeSelecionada.set(cidadeNome);
        template.regiaoSelecionada.set(bairro.regiao);
        template.regiaoCodigoSelecionada.set(bairro.codigoRegiao);

        // inicio da entrevista
        template.inicio = Date.now();

    },
    'change #bairro' (event) {

        let bairroNome = event.target.value,
            bairro = getBairroByNome(bairroNome, template.pesquisa.get());

        template.bairroSelecionado.set(bairro.bairro);

        template.bairroCodigoSelecionado.set(bairro.codigoBairro);

        template.regiaoSelecionada.set(bairro.regiao);

        template.regiaoCodigoSelecionada.set(bairro.codigoRegiao);


    },
    'change #q003-aceite': (event) => {

        let aceite = $('#q003-aceite option:selected').val();

        if(aceite === NAO || aceite === AUSENTE) {

            let res = confirm('Deseja finalizar a entrevista?');

            if(res) {
                salvarQuestionario(template);
            }
            else {
                // Retira a resposta do campo
                limparSelect('q003-aceite');
            }

        }

    },
    'change #q006-condicaoOcupacao': (event) => {

        let aceite = $('#q006-condicaoOcupacao option:selected').val();

        if(aceite === "Alugada") {

            let res = confirm('Você tem a opção de terminar a entrevista. Deseja finalizá-la agora?');

            // Se deseja finalizar a entrevista
            if(res) {
                salvarQuestionario(template);
            }

        }

    },
    'focus #q008-nascimento': (event) => {
        $('#q008-nascimento').datepicker('show');
    },
    // Qualquer campo do tipo tel soh deve aceitar numeros
    'keypress [type="tel"]': function (event) {
        return event.charCode >= 48 && event.charCode <= 57;
    }
});

/**
 * Aplica a validação para todos os campos
 */
const aplicarValidacoes = () => {

    $.validator.addMethod("valueNotEquals", function(value, element, arg){

        let resposta = $(element).find('option:selected').val();

        return resposta != arg;

    }, "Opção selecionada não é válida.");

    let numQuestoes = template.numQuestoes.get(),
        questoesOpcionais = [41], // numero da questao que nao eh obrigatoria
        questoesObrigatorias = [];

    for(let i = 1; i <= numQuestoes; i++) {

        // Se a questao for for obrigatoria
        if(questoesOpcionais.indexOf(i) === -1) {

            let num = i.pad(3);

            $(`[id^="q${num}-"]`).map((i, el) => questoesObrigatorias.push(el.id));

        }
    }

    let rules = {};

    // constroi o objeto de questoes obrigatorias
    questoesObrigatorias.map((questaoId) => {
        rules[questaoId] = {
            required: true
        }
    });

    // Instancia o objeto para validação
    $('form').validate({
        rules: rules
    });

};

/**
 * Aplica uma mascara para inputs que precisam
 */
const aplicarMascaras = () => {

    $('#q001-regiao').mask('99');
    $('#q001-quadra').mask('999');
    $('#q001-lote').mask('9999');
    $('#q001-edificacao').mask('9999');

    $('#q002-cep').mask('99.999-999');

    $('#q004-telefone').mask('(99)99999-9999');

    $('#q008-cpf').mask('999.999.999.99');

    $('#cpfConjugue').mask('999.999.999.99');

};

/**
 * Dado o nome de um bairro, obtém o seu código
 * @param bairroNome
 * @param pesquisa
 * @returns {*}
 */
const getBairroByNome = (bairroNome, pesquisa) => {

    let bairros = pesquisa.bairros;

    for(let i = 0; i < bairros.length; i++) {
        if(bairros[i].bairro === bairroNome) {
            return bairros[i];
        }
    }

    return "ERRO!";

};

/**
 * Foca no primeiro campo de input da tela
 */
const focarPrimeiroCampo = () => {
// Coloca o foco no primeiro campo de input que esteja habilitado
    $($('section.active-question input:enabled')[0]).focus();
};

/**
 * Registra o evento do triggeredBy para todos os elementos que possuem a condicao data-toggle
 */
const aplicarEventosCondicionais = () => {

    let num_questoes = $('section').length - 1; // Subtrai-se um pois ignora a ultima seção, que não contém pergunta

    const removeInvisible = (select, el) => {

        $($(el).parents('fieldset')[0]).removeClass('invisible');
        $(el).val('');

        // coloca os dependentes do select como required
        let id = $(select).attr('id');

        let arr = $('[isTriggeredBy="' + id +'"]');

        if(arr.length > 0) {

            arr.map((i, element) => {

                if($(select).val() === $(element).attr('triggeredByValue')) {
                    $(element).rules('add', {required:true});
                }
            });
        }

    };

    const addInvisible = (select, el, opcaoSelecionada) => {

        let id = el.id;

        $($(el).parents('fieldset')[0]).addClass('invisible');
        $(el).val(opcaoSelecionada);

        // coloca os filhos e seus filhos como invisiveis
        // obtem todos as questoes que sao vinculadas a questao atual
        let arr = $('[isTriggeredBy="' + id +'"]');

        if(arr.length > 0) {

            arr.map((i, element) => {

                if($(select).val() !== $(element).attr('triggeredByValue')) {
                    addInvisible(el, element, "");
                }
                else {
                    $(element).rules('add', {required:true});
                }
            });
        }
    };

    $('select').change(function (evt) {

        let opcaoSelecionada = $(evt.target).find('option:selected').val();

        let deps = $('[isTriggeredBy="' + evt.target.id + '"]');

        // obtem todos os campos com dependencia do campo que foi mudado
        deps.map((i, el) => {

            // se o valor que ele depende eh igual
            if($(el).attr('triggeredByValue') === opcaoSelecionada) {

                removeInvisible(evt.target, el);
            }
            else  {

                addInvisible(evt.target, el, opcaoSelecionada);
            }

        });
    });

};

/**
 * Limpa (seleciona -1) no campo de select
 * @param id
 */
const limparSelect = id => {

    const NAO_RESPONDIDO = -1; // Simbolo que indica que um select box ainda deve ser respondido

    $('#' + id).find(`option[value="${NAO_RESPONDIDO}"]`).prop('selected', true)

}

/**
 * Calcula todas as respostas que foram preenchida no formulario dada um numero de questoes
 * @param NUM_QUESTOES
 * @returns {Array}
 */
const obterRespostas = NUM_QUESTOES => {

    const SECTION_TAG= 'h4';

    let respostas = [];

    // PERGUNTAS DEVEM SER SEQUENCIAIS NO FORMATO q001, q002 ...
    // Abaixo, pego as respostas de todas as pergutnas
    for(let i = 1; i <= NUM_QUESTOES; i++) {

        // Como pergunta tem o codigo com 3 digitos DDD, faz o padding de i
        let num = i.pad(3);

        // pega os elementos de uma determinada questao i
        let $section = $('[id^=q' + num + ']'),
            questao_i = $section.find('input, select'),
            obj = {};

        // preenche a pergunta principal
        let pergunta = $($section.find(SECTION_TAG)[0]).html();

        // Pega a pergunta o descarta o que pertence ao <span>
        pergunta = pergunta.substring(pergunta.indexOf('</span>') + "<span/>".length);

        obj.pergunta = pergunta;
        obj.subPerguntas = [];

        // Percorre o elementos da section
        questao_i.map((i, el) => {

            // Se tiver a classe error, veio do jquery validate, entao ignora
            if(el.className === "error" || el.id.indexOf('-error') !== -1)
                return;

            // obtem a pergunta referente ao input
            let pergunta = $('[for=' + el.id + ']').text(),
                resposta = $(el).val() || $(el).find('option:selected').val() || ""; // Considera campo texto ou select

            // Se a pergunta não foi respondida, seta uma nova resposta
            if(resposta === undefined || resposta === null || resposta === "" || resposta == -1) {
                resposta = NAO_RESPONDIDO;
            }

            // Nao considera elementos selecionados vazios, pois o jquery validate copia a id original
            if(pergunta === undefined || pergunta === null || pergunta === "-") {
                return;
            }

            obj.subPerguntas.push({
                pergunta: pergunta,
                resposta: resposta
            })

        });

        respostas.push(obj);
    };

    // Retorna um array com todas as respostas obtidas por questao
    return respostas;

};

/**
 * Salva o questionario no localStorage
 * @param template
 */
const salvarQuestionario = template => {

    template.isCarregando.set(true);

    // Descobre quanto tempo gastou para realizar a entrevista
    let fimEntrevista = (Date.now() - template.inicio) / 1000; // Em segundos

    template.duracao = fimEntrevista;

    let bairro = template.bairroCodigoSelecionado.get(),
        NUM_QUESTOES = $('section').length - 1; // Subtrai-se um pois ignora a ultima seção, que não contém pergunta

    let respostas = obterRespostas(NUM_QUESTOES);

    let entrevista = {
        duracao: template.duracao,
        bairro: bairro,
        respostas: respostas,
        dataHora: moment().format(),
        localizacao: Geolocation.latLng() || { lat: 0, lng: 0 }
    };

    let pesquisa = template.pesquisa.get();

    // FIXME NAO FUNCIONA MAIS
    const salvarAchouPosicao = (loc) => {

        entrevista.localizacao = { lat: loc.coords.latitude, lng: loc.coords.longitude};

        salvarEntrevistaEmPesquisa(pesquisa._id, entrevista);

    };

    const salvarNaoAchouPosicao = (err) => {

        salvarEntrevistaEmPesquisa(pesquisa._id, entrevista);

    };

    // Salva quando obter a localizacao
    navigator.geolocation.getCurrentPosition(salvarAchouPosicao, salvarNaoAchouPosicao, {timeout: 3000});

};

const salvarEntrevistaEmPesquisa = (id, entrevista) => {

    Pesquisas.update({_id: id},  {$push: {entrevistas : entrevista}} , ( error ) => {

        if ( error ) {
            Bert.alert(error, 'danger');
        }
        else {
            Bert.alert('Entrevista salva com sucesso!', 'success');

            template.isCarregando.set(false);
        }
    });
}

Template.confirmacao.helpers({

    'ehBairroSelecionado': (bairro, bairroSelecionado) => {
        return bairro === bairroSelecionado;
    }

});