import {FlowRouter} from 'meteor/kadira:flow-router';
import '../../../ui/public/index';
import '../../../ui/layouts/default3';
import '../../../ui/public/entrevistas/add_entrevista';
import '../../../ui/public/sincronizar/sincronizar';
import '../../../ui/public/token/token';



const publicRoutes = FlowRouter.group({
  name: 'public'
});

publicRoutes.route( '/', {
    name: 'index',
    action() {
        BlazeLayout.render( 'default3', { yield: 'index' } );
    }
});

publicRoutes.route( '/novaEntrevista', {
    name: 'add_entrevista',
    action() {
        BlazeLayout.render( 'default3', { yield: 'add_entrevista' } );
    }
});

publicRoutes.route( '/sincronizar', {
    name: 'sincronizar',
    action() {
        BlazeLayout.render( 'default3', { yield: 'sincronizar' } );
    }
});

publicRoutes.route( '/token', {
    name: 'token',
    action() {
        BlazeLayout.render( 'default3', { yield: 'token' } );
    }
});