
Template.__checkName("header");
Template["header"] = new Template("Template.header", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n        ", HTML.DIV({
    "class": "col-md-12"
  }, "\n            ", HTML.NAV({
    "class": "navbar navbar-default"
  }, "\n                ", HTML.DIV({
    "class": "container-fluid"
  }, "\n                    ", HTML.Raw('<div class="navbar-header">\n                        <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">\n                            <span class="sr-only">Abrir menu</span>\n                            <span class="icon-bar"></span>\n                            <span class="icon-bar"></span>\n                            <span class="icon-bar"></span>\n                        </button>\n                        <a class="navbar-brand" style="pointer-events: none">Cadastro multifinalitário</a>\n                    </div>'), "\n                    ", HTML.DIV({
    id: "navbar",
    "class": "navbar-collapse collapse"
  }, "\n                        ", HTML.UL({
    "class": "nav navbar-nav"
  }, "\n                            ", Blaze.If(function() {
    return Spacebars.call(view.lookup("existePesquisa"));
  }, function() {
    return [ "\n                                ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(view.lookup("currentRoute"), "novaEntrevista");
      }
    }, "\n                                    ", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "novaEntrevista");
      },
      "class": "novaEntrevista"
    }, "\n                                        ", HTML.SPAN({
      "class": "nav-label novaEntrevista"
    }, "Nova Entrevista"), "\n                                    "), "\n                                "), "\n                            " ];
  }), "\n                            ", HTML.LI({
    id: "token",
    "class": function() {
      return Spacebars.mustache(view.lookup("currentRoute"), "token");
    }
  }, HTML.Raw('<a href="#"><span class="nav-label">Gerenciar token</span></a>')), "\n\n                        "), "\n                        ", HTML.UL({
    "class": "nav navbar-nav navbar-right"
  }, "\n                            ", HTML.Raw('<!--<li class="active"><a href="./">Default <span class="sr-only">(current)</span></a></li>-->'), "\n                            ", HTML.LI({
    "class": function() {
      return Spacebars.mustache(view.lookup("currentRoute"), "sincronizar");
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "sincronizar");
    }
  }, "Sincronizar ", HTML.B("(", Blaze.View("lookup:numEntrevistasNaoSinc", function() {
    return Spacebars.mustache(view.lookup("numEntrevistasNaoSinc"));
  }), ")"))), "\n                        "), "\n                    "), HTML.Raw("<!--/.nav-collapse -->"), "\n                "), HTML.Raw("<!--/.container-fluid -->"), "\n            "), "\n        "), "\n    ");
}));
