// Deny all client-side updates on the Cliente collection
Pesquisas.deny({
    insert() { return false; }, // permite insert
    update() { return false; },
    remove() { return false; },
});
Pesquisas.allow({
    insert: () => {return true},
    update: () => {return true},
    remove: () => {return true}
});