export const Token = new Ground.Collection('token', { connection: null });

// Deny all client-side updates on the Cliente collection
Token.deny({
    insert() { return false; }, // permite insert
    update() { return false; },
    remove() { return false; },
});
Token.allow({
    insert: () => {return true},
    update: () => {return true},
    remove: () => {return true}
});