
Template.__checkName("sincronizar");
Template["sincronizar"] = new Template("Template.sincronizar", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("buscandoDados"));
  }, function() {
    return [ "\n        ", Spacebars.include(view.lookupTemplate("loading")), "\n    " ];
  }, function() {
    return [ "\n        ", HTML.Comment("Se existe alguma entrevista relizada"), "\n        ", Blaze.If(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("entrevistasPorBairro"), "length"));
    }, function() {
      return [ "\n            ", HTML.TABLE({
        "class": "table table-striped"
      }, "\n                ", HTML.THEAD("\n                ", HTML.TR("\n                    ", HTML.TH("Bairro"), "\n                    ", HTML.TH("Quantidade de entrevistas\n                        ", Blaze.If(function() {
        return Spacebars.call(view.lookup("estaConectado"));
      }, function() {
        return [ "\n                            ", HTML.SPAN({
          "class": "sinc",
          id: "sincronizar"
        }, "\n                            ", HTML.I({
          "class": "fa fa-refresh",
          "aria-hidden": "true"
        })), "\n                        " ];
      }, function() {
        return [ "\n                            ", HTML.SPAN({
          "class": "sinc disabled"
        }, HTML.I({
          "class": "fa fa-refresh",
          "aria-hidden": "true"
        })), "\n                        " ];
      }), "\n                    "), "\n                "), "\n                "), "\n                ", HTML.TBODY("\n                ", Blaze.Each(function() {
        return Spacebars.call(view.lookup("entrevistasPorBairro"));
      }, function() {
        return [ "\n                    ", HTML.TR("\n                        ", HTML.TD(Blaze.View("lookup:bairro", function() {
          return Spacebars.mustache(view.lookup("bairro"));
        })), "\n                        ", HTML.TD(Blaze.View("lookup:qut", function() {
          return Spacebars.mustache(view.lookup("qut"));
        })), "\n                    "), "\n                " ];
      }), "\n                "), "\n            "), "\n        " ];
    }, function() {
      return [ "\n            ", HTML.DIV({
        "class": "row text-center"
      }, "\n                ", HTML.DIV({
        "class": "col-md-12"
      }, "\n                    ", HTML.H4("\n                        Nenhum dado a ser sincronizado!\n                        ", Blaze.If(function() {
        return Spacebars.call(view.lookup("estaConectado"));
      }, function() {
        return [ "\n                            ", HTML.SPAN({
          "class": "sinc",
          id: "sincronizar"
        }, "\n                            ", HTML.I({
          "class": "fa fa-refresh",
          "aria-hidden": "true"
        })), "\n                        " ];
      }, function() {
        return [ "\n                            ", HTML.SPAN({
          "class": "sinc disabled"
        }, HTML.I({
          "class": "fa fa-refresh",
          "aria-hidden": "true"
        })), "\n                        " ];
      }), "\n                    "), "\n                "), "\n            "), "\n        " ];
    }), "\n    " ];
  });
}));
