recalcular_posicao_rodape = function() {

    let ch = $('.container').height(),
        wh = $(window).height(),
        logoSize = 30, // logo height
        bottom = 5, // distancia da parte de baixo da tela
        diff = (ch + logoSize) - wh,
        $rodape = $('.rodape');

    if(diff > 0) {
        $rodape.css('bottom', -diff + 'px')
    }
    else {
        $rodape.css('bottom', bottom + 'px')
    }

};